.headerContainer{height:9rem;width:100vw;padding:0 7rem;display:flex;align-items:center}.headerContainer .logoContainer{display:flex;align-items:center;width:fit-content}.headerContainer .logoContainer .logo{width:3.5rem;height:4rem;margin-right:.5rem}.headerContainer .logoContainer .gravl{font-size:2.8rem;font-weight:400;color:#24292f;text-decoration:none}.headerContainer .logoContainer .testing{margin-left:.5rem;font-size:1rem;font-weight:600;color:#319fff;text-transform:uppercase;padding:.3rem;padding-top:.4rem;border-radius:2rem;border:.2rem solid #319fff}.headerContainer .headerRightContainer{flex:auto;display:flex;justify-content:flex-end;align-items:center}.headerContainer .headerRightContainer .loginContainer{display:flex;align-items:center;cursor:pointer}.headerContainer .headerRightContainer .loginContainer .login{font-size:2rem;font-weight:400;margin-right:1rem;color:#24292f}.headerContainer .headerRightContainer .loginContainer .account{width:2.7rem;aspect-ratio:1;border-radius:50%;cursor:pointer}.headerContainer .headerRightContainer .pfpContainer{position:relative;cursor:pointer}.headerContainer .headerRightContainer .pfpContainer .pfp{width:3.5rem;height:3.5rem;border-radius:50%;object-fit:cover}.headerContainer .headerRightContainer .pfpContainer .notifContainer{position:absolute;top:-0.1rem;right:-0.1rem;display:flex;align-items:center;justify-content:center;width:1.5rem;aspect-ratio:1;background-color:#319fff;border-radius:50%;color:#fff;border:.2rem solid #fff}.headerContainer .headerRightContainer .pfpContainer .dropdown{position:absolute;top:5rem;right:0;background-color:#fbfbfb;border-radius:1rem;padding:1rem 2rem;width:10rem;box-shadow:.2rem .3rem .3rem 0 rgba(36,41,47,.25);z-index:100}.headerContainer .headerRightContainer .pfpContainer .dropdown::before{content:"";position:absolute;top:-0.5rem;right:1.25rem;height:1rem;aspect-ratio:1;background-color:#fbfbfb;transform:rotate(45deg);z-index:-1}.headerContainer .headerRightContainer .pfpContainer .dropdown.active{opacity:1;visibility:visible;transform:translateY(0);transition:.3s ease}.headerContainer .headerRightContainer .pfpContainer .dropdown.inactive{opacity:0;visibility:hidden;transform:translateY(-1rem);transition:.3s ease}.headerContainer .headerRightContainer .pfpContainer .dropdown ul li{padding:.5rem 0;color:#757575;font-size:1.5rem;font-weight:300;transition:.3s}.headerContainer .headerRightContainer .pfpContainer .dropdown ul li:last-child{color:#24292f;font-weight:400}.headerContainer .headerRightContainer .pfpContainer .dropdown ul li:hover{color:#24292f}